@import url(https://fonts.googleapis.com/css2?family=Stylish&display=swap);
.App {
  text-align: center;
  
}


.row {
  display: flex;
  justify-content: center;
}

.Form{
	display: flex;
	box-sizing: border-box;
	align-items: center;
	background-color: white;
	border-radius: 0.5em;
	padding-left: 10px;
}
.entri{
	border: 1px transparent;
	border-radius: 4px;
	font-weight: 500;
}
.entri:focus{
	outline: none;
	border: 1px transparent;
}
.submit{
	background-image: url("data:image/png;base64,");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	padding: 15px;
	border: none;
	background-color: transparent;
}

#logoHenry {
  width: 30px;
  border-radius: 10px;

}
.nav_content{
  position: relative;
  width: 100%;
  min-height: 10vh;
}

.fond{
  justify-content: space-around;
  background: rgba(147, 129, 255, 0.80);
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25), 0px 10px 7px rgba(39, 76, 119, 0.34);
  position: fixed;
  width: 100%;
}
.link{
  text-decoration: none;
  color: white;
  font-size: 20px;
  margin: 0;
}
.link:hover{
  text-decoration: none;
  outline: none;
  color: violet;
}
.title{
  font-family: 'Stylish', sans-serif;
  display: flex;
  font-size: 30px;
  align-items: center;
}
.about{
  margin: 0;
  padding: 0;
}



.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.card {
  margin: 20px;
  width: 20rem;
background:transparent;
box-shadow: -3px -4px 23px rgba(179, 221, 244, 0.40), 9px 4px 10px rgba(22, 66, 91, 0.60);
border-radius: 25px;
box-sizing: border-box;



-webkit-animation: animation 865ms linear both;
  animation: animation 965ms  linear both;
  -webkit-animation-name: animation;
          animation-name: animation;
 color: white;
}

.card-title {
  margin-bottom: 15px;
  font-size: 28px;
  font-family: Roboto ;
}

#closeIcon {
  display: flex;
  justify-content: flex-end;
  margin-right: 8px;
  margin-top: 8px;
}

.card-body {
  margin-top: 0px !important;
  padding-top: 0px !important;
}
.boton{
  border: 3px solid brown;
  color: brown;
  background-color: transparent;
  border-radius: 10px;
  padding: 0px 6px;
  font-weight: 600;
}
.boton:hover{
  box-shadow: 0 0 20px red;
  border: 3px solid red;
  color: red;
}
#more{
  border-bottom:1px solid green;
  color: #37A304;
  padding: 0;
  font-size: 40px;
  font-weight: 900;
  background-color: transparent;
  position: absolute;
  top: -10px;
  left: 3px;
 
}
#more:hover{
  text-shadow:#59FF0B 0 0 20px;
  color:#59FF0B;
}
#linkMore{
  padding: 0;
  box-sizing: border-box;

}












